import React, { useEffect, useState } from 'react'
import { Form, Modal, Select } from "antd";
import { reasonsData } from "./Data";

const { Option } = Select;

function ProductDeleteForm(props) {
    const [ReasonValue, setReasonValue] = useState("");
    const onReasonChange = (value) => { setReasonValue(value) };

    useEffect(() => {
        if (props.admin) {
            setReasonValue("")
        }
    }, [props.admin])

    const handleOk = () => {
        const reason = {
            reason: ReasonValue
        }

        props.handleOk(reason);
        setReasonValue("");
    }

    const handleCancel = () => {
        props.handleCancel();
        setReasonValue("");
    }

    return (
        <div>
            <Modal
                title="Product"
                visible={props.visible}
                onOk={handleOk}
                onCancel={handleCancel}
                destroyOnClose={true}
            >
                <Form>
                    <label>:</label>&nbsp;
                    {props.admin ?
                    <Select
                        style={{ width: 200 }}
                        placeholder
                        onChange={onReasonChange}
                        value={ReasonValue}
                    >
                        <Option value={""}></Option>
                        <Option value={""}></Option>
                    </Select> :
                    <Select
                        style={{ width: 200 }}
                        placeholder
                        onChange={onReasonChange}
                        value={ReasonValue}
                    >
                        {reasonsData.map((reason) => <Option key={reason.key} value={reason.value}>{reason.value}</Option>)}
                    </Select>
                    }
                    <br /><br />
                </Form>
            </Modal>
        </div>
    )
}

export default ProductDeleteForm
